	module.exports = {"data":{"markdownRemark":{"html":"<p>Làm front end được kha khá năm, đứng trung gian giữa anh developer và anh designer, được tiếp xúc với rất nhiều designer từ cao cấp tới mới ra trường, mình có thể khẳng định là những ảnh designer dù là tự học hay được đào tạo bài bảng qua trường lớp, mỗi anh điều có một kiểu làm việc rất riêng, trong giới designer, đặc biệt là web design, sẽ chẳng anh nào đi theo một cái guide chung nào cả, khác hẳng với developer, nơi mà mọi thứ luôn được chuẩn hóa từng ngày, một anh developer mới vào công ty được thẩy vô trong một cái dự án bự tổ chảng, sau vài ngày training những \"luật lệ\" được đề ra từ lớp người đi trước là ảnh có thể follow theo và bắt đầu làm việc với một cách mượt mà. Ở góc độ của một người đứng giữa, mình luôn cho rằng anh developer luôn luôn là người nên đặt ra các luật lệ mà anh designer phải tuân theo, chứ không phải ngược lại.</p>","timeToRead":1,"excerpt":"Làm front end được kha khá năm, đứng trung gian giữa anh developer và anh designer, được tiếp xúc với rất nhiều designer từ cao cấp tới mới…","frontmatter":{"title":"Thời đại của Pixel Perfect Design đã hết từ lâu","cover":"","date":"2017-03-06","category":"ux-ui","tags":["css","ux-ui"],"desc":"Làm front end được kha khá năm, đứng trung gian giữa anh developer và anh designer, được tiếp xúc với rất nhiều designer từ cao cấp tới mới ra trường, mình có thể khẳng định là những ảnh designer dù là tự học hay được đào tạo bài bảng qua trường lớp"},"fields":{"slug":"/2017-03-06-thoi-dai-cua-pixel-effect-design-da-het-tu-lau"}}},"pathContext":{"slug":"/2017-03-06-thoi-dai-cua-pixel-effect-design-da-het-tu-lau","prev":{"frontmatter":{"title":"Interview React Developer thì hỏi gì?","desc":"Một vài câu hỏi để kiểm tra mức độ am hiểu react của một lập trình viên frontend","type":"post","category":"javascript","tags":["javascript","react"]},"fields":{"slug":"/2017-07-17-interview-react-developer-thi-hoi-gi"}},"next":{"frontmatter":{"title":"Giới thiệu React Router","desc":"React không phải một framework, nó chỉ là một thư viện javascript, bởi vì vậy nó không thể giải quyết tất cả các yêu cầu một application. Việc mà React làm rất tốt là tạo component và cách thức quản lý state của component, nhưng để tạo một Single Page App (sẽ viết tắt là SPA) phức tạp, nó cần những thư viện khác nữa, đầu tiền cần nhắc đến là React Router","type":"post","category":"react","tags":["javascript","react"]},"fields":{"slug":"/2017-07-31-react-router"}}}}

